const charaTag = ".splide__list__succubus";

const charaSSHTML =
	'<a href="" data-fancybox="succubi"><img class="gtm__charaScreenShotImg" src="" alt="" /></a>';

$(document).ready(function () {
	//checks to see if template tags are supported
	if ("content" in document.createElement("template")) {
		const charatemplate = document.querySelector(
			"#succubusTemplateSplide"
		);

		//screenshots
		for (let succubus of succubi) {
			console.log(`Added slide succubus: ${succubus.name}`)
			//clone the template code
			let clone = charatemplate.content.cloneNode(true);
			let charaCar = clone.querySelector(".succubus-slide");
			/* charaCar.querySelector("h2").innerHTML = succubus.name;
			charaCar.querySelector("h2").style.color = succubus.color; */
			charaCar
				.querySelector(".succubi-bio > img")
				.setAttribute("src", sign_path + succubus.file);
			charaCar.querySelector("p").innerHTML = succubus.description;
			charaCar
				.querySelector(".succubus-portrait > img")
				.setAttribute("src", chara_path + succubus.file);

			document.querySelector(charaTag).append(charaCar);
		}
	} else {
		console.log("template not supported");
	}

	let charaSplide = new Splide(".succubus-splide", {
		type: "loop",
		pagination: false,
		perPage: 1,
		focus: "center",
		padding: "0",
		drag: true,
	}).mount();
});
